<template>
  <div class="container">
    <div class="dew-figure-container">
      <div class="dew-figure-header clearfix">
        <span class="game-table">{{vid}}桌</span>
        <span class="timer-text">
        <i class="icon-time"></i>
        <span class="monospaced-font">00<span>:</span>{{countdown>9?countdown:`0${countdown}`}}</span>
      </span>
      </div>
      <div class="scoreboard clearfix">
      <span class="scoreboard-item">
        <span>庄</span>
        <span>{{banker}}</span>
      </span>
        <span class="scoreboard-item">
        <span>和</span>
        <span>{{tie}}</span>
      </span>
        <span class="scoreboard-item">
        <span>闲</span>
        <span>{{player}}</span>
      </span>
      </div>
      <div class="canvas-container">
        <div v-show="showShuffle" class="shuffle-wrapper">
          <span class="shuffle-text">洗牌中</span>
        </div>
        <div v-if="showLoading" class="loading-wrapper">
          <span class="loading-text">loading...</span>
        </div>
        <div v-show="!showShuffle" id="canvasWrapper1" class="canvas-wrapper">
          <canvas id="canvas1" class=""></canvas>
        </div>
      </div>
      <div class="button-wrapper">
        <button @click="()=>null" class="button">
          <span class="button-text">快速入座</span>
        </button>
      </div>
    </div>
  </div>
</template>

<script>
  import { mapState } from 'vuex'

  export default {
    name: 'dewFigure',
    data() {
      return {
        ctx: null,
        vid: 'B001',
        columns: 16,
        rows: 6,
        cellSizeWidth: 23,
        cellSizeHeight: 23,
        lineWidth: 1,
        resultColor: {
          banker: '#d55263',
          player: '#6e8df7',
          tie: '#4ea950'
        },
        showLoading: true,
        showShuffle: true,
        resultList: [],
        timer: null,
        countdown: 0,
        timerText: null,
        lastGameCode: null
      }
    },
    computed: {
      ...mapState(['socketResultList']),
      origin() {
        return this.lineWidth / 2
      },
      cellHeight() {
        return this.cellSizeHeight + this.lineWidth
      },
      cellWidth() {
        return this.cellSizeWidth + this.lineWidth
      },
      gridWidth() {
        return this.lineWidth + (this.columns * this.cellWidth)
      },
      gridHeight() {
        return this.lineWidth + (this.rows * this.cellHeight)
      },
      banker() {
        return this.resultList.filter(item => item.result === 'banker').length
      },
      player() {
        return this.resultList.filter(item => item.result === 'player').length
      },
      tie() {
        return this.resultList.filter(item => item.result === 'tie').length
      }
    },
    mounted() {
      let canvas = document.getElementById('canvas1')
      let canvasWrapper = document.getElementById('canvasWrapper1')
      let ctx = canvas.getContext('2d')
      let dpr = window.devicePixelRatio
      if (dpr) {
        dpr = Math.ceil(dpr)
        dpr = dpr > 2 ? dpr : 4
        canvas.width = this.gridWidth * dpr
        canvas.height = this.gridHeight * dpr
        canvas.style.width = this.gridWidth + 'px'
        canvas.style.height = this.gridHeight + 'px'
        canvasWrapper.style.width = this.gridWidth + 'px'
        canvasWrapper.style.height = this.gridHeight + 'px'
        ctx.scale(dpr, dpr)
      }
      this.ctx = ctx
      this.formatResultList(this.socketResultList)
    },
    methods: {
      // 画网格线
      drawGrid(ctx) {
        let lineColor = '#202020'
        // 画竖线（列）
        for (let x = this.origin; x <= this.gridWidth; x += this.cellWidth) {
          ctx.beginPath()
          ctx.strokeStyle = lineColor
          ctx.lineWidth = this.lineWidth
          ctx.moveTo(x, 0)
          ctx.lineTo(x, this.gridHeight)
          ctx.stroke()
        }
        // 画横线 （行）
        for (let y = this.origin; y <= this.gridHeight; y += this.cellHeight) {
          ctx.beginPath()
          ctx.strokeStyle = lineColor
          ctx.lineWidth = this.lineWidth
          ctx.moveTo(0, y)
          ctx.lineTo(this.gridWidth, y)
          ctx.stroke()
        }
      },
      // 画圆
      drawCircle(ctx, x, y, color) {
        let circleFillColor = '#141414'
        let circleBorderColor = color
        let circleBorderWidth = 3
        let circleRadii = 8
        ctx.beginPath()
        ctx.arc(x, y, circleRadii, 0, 360, false)
        ctx.fillStyle = circleFillColor
        ctx.strokeStyle = circleBorderColor
        ctx.lineWidth = circleBorderWidth
        ctx.fill()
        ctx.stroke()
      },
      // 画斜线
      drawLine(ctx, start, end, width, color) {
        ctx.beginPath()
        ctx.moveTo(start[0], start[1])
        ctx.lineTo(end[0], end[1])
        ctx.lineWidth = width
        ctx.strokeStyle = color
        ctx.stroke()
      },
      // 画文字
      drawText(cxt, x, y, text, font) {
        cxt.beginPath()
        cxt.font = font
        cxt.fillStyle = '#fff'
        cxt.textAlign = 'center'
        cxt.textBaseline = 'middle'
        cxt.fillText(text, x, y)
        cxt.stroke()
      },
      // 绘制露珠图
      drawDewFigure(ctx) {
        let bigRoadData = this.formatBigRoadData(this.resultList).slice(-(this.columns - 1))
        ctx.clearRect(0, 0, this.gridWidth, this.gridHeight)
        this.drawGrid(ctx)
        bigRoadData.forEach((column, columnIndex) => {
          column.forEach((item, itemIndex) => {
            let x = (columnIndex + 1) * this.cellWidth - this.cellSizeWidth / 2
            let y = (itemIndex + 1) * this.cellHeight - this.cellSizeHeight / 2
            this.drawCircle(ctx, x, y, item.color)
            if (item.tieNumber) {
              let offset = 3
              let start = columnIndex * this.cellWidth + this.origin
              let end = itemIndex * this.cellHeight + this.origin
              this.drawLine(ctx, [start + offset, end + this.cellHeight - offset], [start + this.cellWidth - offset, end + offset], 2, this.resultColor['tie'])
              if (item.tieNumber > 1 && item.tieNumber < 10) {
                this.drawText(ctx, Math.ceil(x), Math.ceil(y), '2', '16px Calibri')
              } else if (item.tieNumber > 10) {
                this.drawText(ctx, x, Math.ceil(y), '18', '12px Calibri')
              }
            }
          })
        })
        ctx.closePath()
      },
      // 格式化当前桌台历史数据
      formatItemData(item) {
        let obj = {
          pair: item['pair'] // 对子 0：无、1：庄对、2：闲对、3：庄闲对
        }
        if (item['banker_val'] > item['player_val']) {
          obj.result = 'banker' // 庄赢
        } else if (item['banker_val'] < item['player_val']) {
          obj.result = 'player' // 闲赢
        } else {
          obj.result = 'tie' // 和
        }
        return obj
      },
      // 格式化大路露珠图数据
      formatBigRoadData(list = []) {
        let column // 当前列
        let columnLastItem // 当前列最后一项
        let resultFlag // 结果标志
        let tieNumber = 0 // 和局次数
        let bigRoadList = [] // 大路图数据
        let isAddNewColumn = false // 是否新增一行
        list.forEach(item => {
          column = bigRoadList[bigRoadList.length - 1]
          columnLastItem = column && column[column.length - 1]
          isAddNewColumn = item.result !== 'tie' &&
            (resultFlag !== item.result || (column && column.length >= this.rows))
          if (isAddNewColumn) {
            bigRoadList.push([])
          }
          column = bigRoadList[bigRoadList.length - 1]
          if (item.result === 'tie') {
            if (columnLastItem) {
              columnLastItem.tieNumber += 1
              tieNumber = 0
            } else {
              tieNumber += 1
            }
            resultFlag = (columnLastItem && columnLastItem.result)
          } else {
            column.push({
              ...item,
              tieNumber,
              color: this.resultColor[item.result]
            })
            tieNumber = 0
            resultFlag = item.result
          }
        })
        return bigRoadList
      },
      // 倒计时
      countdownFunc(text) {
        this.timerText = null
        clearInterval(this.timer)
        if (this.countdown === 0) {
          this.timerText = text
        }
        this.timer = setInterval(() => {
          if (this.countdown > 0) {
            this.countdown--
          } else {
            this.timerText = text
            clearInterval(this.timer)
          }
        }, 1000)
      },
      // 洗牌中
      newShoes() {
        this.countdown = 0
        this.countdownFunc('洗牌中')
        this.showShuffle = true
        this.resultList = []
        this.drawDewFigure(this.ctx)
      },
      // 开始一局
      newRound(seconds) {
        this.showShuffle = false
        this.countdown = seconds
        this.countdownFunc('结算中')
      },
      // 结束一局
      closeRound(obj) {
        this.countdown = 0
        this.countdownFunc('发牌中')
        this.resultList = [...this.resultList, this.formatItemData(obj)]
        this.drawDewFigure(this.ctx)
      },
      // 连接后推送的历史数据
      formatResultList(obj) {
        obj = obj[this.vid] || {}
        console.log(obj)
        let roundRes = obj.roundRes || {}
        let key
        let item
        let list = []
        for (key in roundRes) {
          if (roundRes.hasOwnProperty(key)) {
            item = this.formatItemData(roundRes[key])
            list.push(item)
          }
        }
        if (!list.length) {
          this.newShoes()
        } else {
          this.resultList = list
          this.newRound(obj.seconds)
          this.drawDewFigure(this.ctx)
        }
        this.showLoading = false
      }
    },
    sockets: {
      'connect_error'(e) {
        console.log('connect_error:\n', e)
      },
      'connect_timeout'(e) {
        console.log('connect_timeout:\n')
        console.log(e)
      },
      'reconnect_error'(e) {
        console.log('reconnect_error\n')
        console.log(e)
      },
      'reconnect_failed'(e) {
        console.log('reconnect_failed\n')
        console.log(e)
      },
      'result list'(obj) {
        this.formatResultList(JSON.parse(obj))
      },
      'new shoes'(obj) {
        obj = JSON.parse(obj)
        if (obj.vid === this.vid) {
          this.newShoes()
          console.log('new shoes:\n', obj)
        }
      },
      'new round'(obj) {
        obj = JSON.parse(obj)
        if (obj.vid === this.vid) {
          this.newRound(obj.seconds)
          console.log('new round:\n', obj)
        }
      },
      'close round'(obj) {
        obj = JSON.parse(obj)
        if (obj.vid === this.vid && this.lastGameCode !== obj['gmcode']) {
          this.closeRound(obj)
          console.log('close round:\n', obj)
        }
      }
    }
  }
</script>

